/*mutation函数名称常量*/

export const RECEIVE_ADDRESS = 'receive_address' // 接收地址信息
export const RECEIVE_CATEGORYS = 'receive_categorys' // 接收商品分类数组（用于轮播列表显示）
export const RECEIVE_SHOPS = 'receive_shops' // 接收商家数组
export const RECEIVE_USER = 'receive_user' // 接收用户信息
export const RESET_USER = 'reset_user' // 重置用户信息
export const RECEIVE_GOODS = 'receive_goods' // 接收商家商品数组
export const RECEIVE_RATINGS = 'receive_ratings' // 接收商家评价数组
export const RECEIVE_INFO = 'receive_info' // 接收商家详细信息
export const INCREMENT_FOOD_COUNT = 'increment_food_count' // 增加food的count
export const DECREMENT_FOOD_COUNT = 'decrement_food_count' // 减少food的count
export const CLEAR_CART = 'clear_cart'  // 清空购物车


