/*包含n个用于间接更新状态数据方法的对象:发送ajax请求从后台获取数据*/
import {reqAddress,reqFoodCategorys,reqShops,reqUser,reqLogout,reqShopGoods,reqShopRatings,reqShopInfo} from '../api'
import {
  RECEIVE_ADDRESS,
  RECEIVE_CATEGORYS,
  RECEIVE_SHOPS,
  RECEIVE_USER,
  RESET_USER,
  RECEIVE_GOODS,
  RECEIVE_RATINGS,
  RECEIVE_INFO,
  INCREMENT_FOOD_COUNT,
  DECREMENT_FOOD_COUNT,
  CLEAR_CART
} from './mutation-types.js'

export default {
  //1. 发送ajax请求，根据经纬度从后台异步获取地址
  async getAddress ({commit, state}) {
    const geohash = state.latitude +','+ state.longitude
    const result = await reqAddress(geohash)
    if (result.code === 0) {
      const address = result.data
      // commit给mutation
      commit(RECEIVE_ADDRESS, {address:address})
    }
  },
  //2. 发送ajax请求，从后台异步获取商品分类
  async getFoodCategorys ({commit}) {
    const result = await reqFoodCategorys()
    if (result.code === 0) {
      const categorys = result.data
      // commit给mutation
      commit(RECEIVE_CATEGORYS, {categorys})
    }
  },  
  //3.发送ajax请求，根据经纬度从后台异步获取商铺列表
  async getShops ({commit, state}) {
    const {latitude, longitude} = state
    const result = await reqShops(longitude, latitude)
    if (result.code === 0) {
      const shops = result.data
      // commit给mutation
      commit(RECEIVE_SHOPS, {shops})
    }
  },
  //4.同步保存用户信息的action,实现登录
  saveUser ({commit}, user) {
    commit(RECEIVE_USER, {user})
  },
  //5.异步获取用户信息的action，携带cookie相关信息，实现自动登录
  async getUser ({commit}) {
    const result = await reqUser()
    if(result.code===0) {
      const user = result.data
      commit(RECEIVE_USER, {user})
    }
  },

  //6.异步退出登陆的action
  async logout ({commit}) {
    const result = await reqLogout()
    if(result.code===0) {
      commit(RESET_USER)
    }
  },
  //7.异步获取商家商品列表（数据来自mock数据）
  async getShopGoods({commit}, cb) {
    const result = await reqShopGoods()
    if(result.code===0) {
      const goods = result.data
      commit(RECEIVE_GOODS, {goods})
      // 如果组件中传递了接收消息的回调函数, 数据更新后, 调用回调通知调用的组件
      typeof cb ==='function' && cb()
    }
  },      
  //8.异步获取商家评价列表（数据来自mock数据）
  async getShopRatings({commit}, cb) {
    const result = await reqShopRatings()
    if(result.code===0) {
      const ratings = result.data
      commit(RECEIVE_RATINGS, {ratings})
      typeof cb ==='function' && cb()
    }
  },
  //9发送请求异步获取商家详细信息（数据来自mock数据）
  async getShopInfo({commit}, cb) {
    const result = await reqShopInfo()
    if(result.code===0) {
      const info = result.data
      commit(RECEIVE_INFO, {info})
      typeof cb ==='function' && cb()
    }
  },
  //10同步更新购物车中foodcount的数量
  updateFoodCount({commit},{isAdd, food}){
    if (isAdd) {//增加
      commit(INCREMENT_FOOD_COUNT,{food})
    }else {//减少
      commit(DECREMENT_FOOD_COUNT,{food})
    }
  },
  // 11清空购物车
  clearCart({commit}){
    commit(CLEAR_CART)
  }
}


