<!--全局底部导航组件：应用到各个组件中显示内容相同-->
<template>
<footer class="footer_guide border-1px">
  <div class="guide_item" @click="goto('/msite')" :class="{on:$route.path==='/msite'}">
    <span class="item_icon">
      <i class="iconfont icon-waimai"></i>
    </span>
    <span>外卖</span>
  </div>
  <div class="guide_item" @click="goto('/search')" :class="{on:$route.path==='/search'}">
    <span class="item_icon">
      <i class="iconfont icon-search"></i>
    </span>
    <span>搜索</span>
  </div>
  <div class="guide_item" @click="goto('/order')" :class="{on:$route.path==='/order'}">
    <span class="item_icon">
      <i class="iconfont icon-dingdan"></i>
    </span>
    <span>订单</span>
  </div>
  <div class="guide_item" @click="goto('/profile')" :class="{on:$route.path==='/profile'}">
    <span class="item_icon">
      <i class="iconfont icon-geren"></i>
    </span>
    <span>我的</span>
  </div>
</footer>
</template>

<script>
  export default{
    methods:{
      goto(path){
        this.$router.replace(path)
      }
    }

  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../assets/stylus/minxins'
  .footer_guide
    top-border-1px(#e4e4e4)
    position fixed
    z-index 100
    left 0
    right 0
    bottom 0
    background-color #fff
    width 100%
    height 50px
    display flex
    .guide_item
      display flex
      flex 1
      text-align center
      flex-direction column
      align-items center
      margin 5px
      color #999999
      &.on
        color #02a774
      span
        font-size 12px
        margin-top 2px
        margin-bottom 2px
        .iconfont
          font-size 22px
</style>




