/*
接口请求函数模块
 */
import ajax from './ajax'
// const base = '/api'
const base = ''
//1、根据经纬度请求获取位置详情
export const reqAddress = (geohash) => ajax(base+'/position/'+geohash)

//2、请求获取商品分类列表（轮播图显示数据）
export const reqFoodCategorys = () => ajax(base+'/index_category')

//3、根据经纬度请求获取商家列表
export const reqShops = (longitude, latitude) => ajax(base+'/shops', {latitude, longitude})

//4、根据电话号码发送请求获取短信验证码
export const reqSendCode = phone => ajax(base+'/sendcode', {phone})

// 5、根据电话号码及短信验证码发送请求获取登录信息
export const reqSmsLogin = (phone, code) => ajax(base+'/login_sms', {phone, code}, 'POST')

// 6、根据用户名、密码及图形验证码发送请求获取登录信息
export const reqPwdLogin = ({name, pwd, captcha}) => ajax(base+'/login_pwd', {name,pwd,captcha}, 'POST')

// 7.发送请求获取用户信息(根据会话)
export const reqUser = () => ajax(base+'/userinfo')

//8.发送请求退出登陆
export const reqLogout = () => ajax(base+'/logout')

//9.发送请求获取商家的相关信息（数据实际来自mock的数据 接口来自mock模块，发送的请求被mockjs拦截）
export const reqShopGoods = () => ajax('/goods')
export const reqShopRatings = () => ajax('/ratings')
export const reqShopInfo = () => ajax('/info')



