/*包含n个用于直接更新状态数据方法的对象*/
import Vue from 'vue'
import {
  RECEIVE_ADDRESS,
  RECEIVE_CATEGORYS,
  RECEIVE_SHOPS,
  RECEIVE_USER,
  RESET_USER,
  RECEIVE_GOODS,
  RECEIVE_RATINGS,
  RECEIVE_INFO,
  INCREMENT_FOOD_COUNT,
  DECREMENT_FOOD_COUNT,
  CLEAR_CART
} from './mutation-types.js'

export default {
  //1.根据经纬对获取地址
  [RECEIVE_ADDRESS](state,{address}){
    state.address = address
  },
  //2.获取商品分类列表
  [RECEIVE_CATEGORYS](state,{categorys}){
    state.categorys = categorys
  },
  //3.根据经纬对获取商铺列表
  [RECEIVE_SHOPS](state,{shops}){
    state.shops = shops
  }, 
  // 4.获取用户信息
  [RECEIVE_USER] (state, {user}) {
    state.user = user
  },
  // 5.重置用户信息
  [RESET_USER] (state) {
    state.user = {}
  },
  //6.接受商家商品数组 
  [RECEIVE_GOODS](state,{goods}){
    state.goods = goods
  },
   //7.接受商家评论数组 
   [RECEIVE_RATINGS](state,{ratings}){
    state.ratings = ratings
  },
   //8.接受商家详情信息 
   [RECEIVE_INFO](state,{info}){
    state.info = info
  }, 
  //9.购物车增加食物
  [INCREMENT_FOOD_COUNT](state, {food}){
    if (food.count) {
      food.count++
    }else{//首次添加，是给food对象添加新的count属性,初始值为1,使用vue的set方法
      Vue.set(food, 'count', 1)
      // 增加食物时，将新的food添加到购物车数组中
      state.cartFoods.push(food)
    }
  },
  //10.购物车减少食物
  [DECREMENT_FOOD_COUNT](state, {food}){
    if (food.count) {
      food.count--
      // 当食物数量减少到零时，将当前食物的下标从购物车数组中移出，从而删除食物
      if(food.count === 0) {
        state.cartFoods.splice(state.cartFoods.indexOf(food),1)
      }
    }
  },
  //11.清空购物车
  [CLEAR_CART](state){
    // 将购物车数组中cartFoods所有food的数量置为0
    state.cartFoods.forEach(food => food.count = 0)
    // 重置cartFoods
    state.cartFoods = []
  }
}


